@import '/src/common/scss/common.scss';
@import '/src/components/logo/logo.scss';
@import '/src/components/header/header.scss';

.movies.section {
  min-height: 100%;
  padding: 40px 0 144px;

  background-color: #212328;
}
.movies__container {
  display: flex;
  flex-direction: column;
  gap: 32px;
  max-width: 1440px;
  // width:  1440px;
  min-width: 0px;
  padding: 0 80px;
  margin: 0 auto;
  overflow: hidden;
}
.movies__line {
  width: 100%;
  height: 1px;
  margin-top: 16px;
  background: #464646;
}
.movies__title {
  display: flex;
  align-items: center;
  gap: 8px;

  color: #fff;
  font-size: 24px;
  font-style: normal;
  font-weight: 500;
  line-height: 140%;
}
.users__list {
  display: flex;
  // justify-content: center;
  flex-wrap: wrap;
  --gap: 24px;
  gap: var(--gap);
}
.users__item {
  --items: 4;
  flex-basis: calc((100% - var(--gap) * (var(--items) - 1)) / var(--items)) !important;
  width: calc((100% - var(--gap) * (var(--items) - 1)) / var(--items)) !important;
}

.user {
  position: relative;
  display: flex;
  flex-direction: column;
  
  cursor: pointer;
  &::before {
    content: '';
    position: absolute;
    width: 100%;
    height: 100%;
  
    background-color: rgba(0, 0, 0, 0.5);
    z-index: 1;
    opacity: 0;
    transition: all 300ms ease-out;
  }
}
.movie:hover::before,
.movie:focus::before { opacity: 1;}

.movie__img-wrap {
  position: relative;
  max-width: 100%;
}
.movie__img-wrap::before {
  content: '';
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);

  display: block;
  width: 72px;
  height: 72px;

  background-image: url('/static/images/play.svg');
  transition: all 300ms ease-out;
  z-index: 1;
  opacity: 0;
}
.movie:hover .movie__img-wrap::before,
.movie:focus .movie__img-wrap::before { opacity: 1;}


.movie__img {
  object-fit: cover;
  max-width: 100%;
  width: 100%;
}
.movie__viewing,
.movie__rating {
  position: absolute;
  top: 16px;

  display: flex;
  width: 77px;
  height: 24px;
  padding: 4px 16px 4px 8px;
  justify-content: center;
  align-items: center;
  gap: 10px;

  color: #fff;
  font-size: 14px;
  line-height: normal;

  background: #464646;
}

.movie__viewing {
  border-radius: 0px 16px 16px 0px;
}
.movie__rating {
  right: 0;
  border-radius: 16px 0px 0px 16px;
}

.movie__views {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 8px;
  padding: 4px 16px;

  color: #B1AAAA;
  font-size: 12px;
  font-weight: 400;
  line-height: 130%;

  background-color: #464646;
}



.movie__btn {
  position: absolute;
  bottom: 16px;

  display: flex;
  justify-content: center;
  align-items: center;
  width: 40px;
  height: 40px;

  color: #fff;

  border-radius: 50%;
  background: #464646;
}
.movie__btn:nth-of-type(1) { left: 16px; }
.movie__btn:nth-of-type(2) { left: calc(16px + 8px + 40px);}
.movie__btn:nth-of-type(3) { left: calc(16px + 8px + 40px + 8px + 40px); }


.user {
  position: relative;
  border: 1px solid #464646;
  background-color: #212328;

  &:hover, :focus { box-shadow: 0px 4px 4px 0px #A8A8A8;}
}
.user__img {
  position: absolute;
  top: 16px;
  left: 50%;
  transform: translateX(-50%);
  z-index: 1;
}
.user__bg {
  width: 100%;
  height: 150px;

  background: linear-gradient(288deg, #03050A 19.2%, #75758E 98.38%);
}
.user__bg--men {
  background: linear-gradient(291deg, #090B0E -14.32%, #343943 98.83%);
}
.user__desc {
  display: flex;
  align-items: center;
  flex-direction: column;
  width: 100%;
  // height: 416px;
  padding: 42px 24px;

  color: #B1AAAA;
  font-size: 12px;
  font-style: normal;
  font-weight: 400;
  line-height: 130%
}
.user__name {
  color: #fff;
  font-size: 20px;
  font-style: normal;
  font-weight: 500;
  line-height: normal;
}
.user__age {
  margin-bottom: 15px;
}
.user__statistics {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  height: 34px;
  padding: 4px 16px;

  color:  #FFF;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 140%;

  border: 1px solid  #464646;

  &:not(:last-of-type) {
    margin-bottom: 8px;
  }
}


.swiper {
  min-width: 0px;
}
.movies-swiper-btn {
  position: absolute;
  top: 128px;

  display: flex;
  width: 48px;
  height: 48px;
  padding: 8px;
  justify-content: center;
  align-items: center;
  gap: 10px;
  flex-shrink: 0;

  color: #fff;

  border-radius: 50000000px;
  background: rgba(33, 35, 40, 0.90);
  z-index: 100;
}
.movies-swiper-btn-next {
  right: 0;
}


.profile.section {
  padding: 24px 0 0;

  background-color: #212328;
}
.profile__container {
  display: flex;
  flex-direction: row;
  gap: 24px;

  border: 1px solid #464646;
}
.profile__avatar-wrap {
  position: relative;
}
.profile__avatar-wrap svg {
  position: absolute;
  top: 8px;
  right: 8px;
}
.profile__avatar {
  object-fit: cover;
  height: 100%;
}
.profile__list {
  padding: 20px 0;
  display: flex;
  flex-direction: column;
  gap: 4px;
}
.profile__item {
  color:  #B1AAAA;
  font-size: 16px;
  font-weight: 400;
  line-height: 140%; /* 22.4px */
}
.profile__name {
  color:  #FFF;
  font-size: 24px;
  font-weight: 500;
  line-height: 140%;
}
.profile__btn--edit {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 48px;
  padding: 8px 32px;
  gap: 8px;
  margin: auto 0 0 auto;

  color:  #FFF;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 140%;
  background: #2B2D33;
}
.profile__btn--edit svg {
  flex: 0 0 24px;
}

.pagination {
  margin: 40px auto 0;
}
.pagination__list {
  display: flex;
  gap: 8px;
}
.pagination__btn {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 24px;
  min-width: 24px;
  // max-width: 48px;
  padding: 0 8px;

  color: #fff;
  font-size: 14px;
  line-height: normal;
  background: #464646;
}

.filters__box {
  display: flex;
  gap: 16px;
}

.filters-dropdown {
  position: relative;
}
.filters-dropdown__list {
  // position: relative;
  display: flex;
  flex-direction: column;

  border: 1px solid #fff;
}
.filters-dropdown__list::before {
  content: '';
  position: absolute;
  top: 8px;
  right: 16px;

  display: block;
  width: 16px;
  height: 16px;
  transition: 300ms linear;

  background-image: url('/static/images/filter-arrow.svg');
}
.filters-dropdown__list.isActive::before {
  // display: none;
  transform: rotate(180deg);
}
.filters-dropdown__item {
  display: flex;
  width: 100%;
  padding: 8px 16px;
  align-items: center;

  font-size: 12px;
  line-height: normal;
  color: #fff;

  .filters-dropdown__list.isActive &:not(:first-of-type) {
    border-top: 1px solid #fff;
  }
  &.isHidden {
    display: none;
  }

  &:hover {background: #464646;}
}


.filters-perpage {
  margin-left: auto;
}
.filters-perpage__list {
  position: absolute;
  right: 0;
  width: 48px;

  background-color: #212328;
  z-index: 2;
}
.filters-perpage__list.filters-dropdown__list::before {
  right: 8px;
}
.filters-perpage__item.filters-dropdown__item {
  padding: 8px 24px 8px 8px;
}

